<template>
  <v-dialog :value="show" max-width=500 persistent=true>
    <v-card>
      <v-card-text>
        <v-text-field 
          label="Lokasi"
          v-model="item.lokasi"></v-text-field>

        <v-text-field 
          label="Longitude"
          type="number"
          v-model="item.long"></v-text-field>

        <v-text-field 
          label="Latitude"
          type="number"
          v-model="item.lat"></v-text-field>

        <v-text-field 
          label="Curah Hujan"
          type="number"
          v-model="item.curahHujan"
          readonly></v-text-field>

        <v-select 
          :items="selectItems.sistemPengairan" 
          label="Sistem Pengairan" 
          v-model="item.sistemPengairan">
        </v-select>

        <v-select :items="selectItems.sumberAir" label="Sumber Air"
          v-model="item.sumberAir">
        </v-select>

        <v-select :items="selectItems.kemiringanLahan" label="Kemiringan Lahan"
          v-model="item.kemiringanLahan">
        </v-select>

        <v-select :items="selectItems.teksturTanah" label="Tekstur Tanah"
          v-model="item.teksturTanah">
        </v-select>
        <v-select :items="selectItems.corganik" label="C-Organik"
          v-model="item.corganik">
        </v-select>
        <v-select :items="selectItems.bahayaBencana" label="Bahaya Bencana"
          v-model="item.bahayaBencana">
        </v-select>

        <v-text-field 
          label="Kedalaman Tanah"
          type="number"
          v-model="item.kedalamanTanah">
        </v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-btn flat large color="blue" class="my-4"
              @click="$emit('add-save', item)">Simpan</v-btn>
        <v-btn flat large color="red" class="my-4"
              @click="$emit('add-cancel')">Batal</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { selectItems } from './service'

const defaultActive = {
  lokasi: 'oesapa',
  lat: -10.179173,
  long: 123.591715,
  curahHujan: 448,
  sistemPengairan: 0,
  sumberAir: 0,
  kemiringanLahan: 0,
  teksturTanah: 0,
  corganik: 0,
  bahayaBencana: 0,
  kedalamanTanah: 48
}

export default {
  props: ['show'],
  data () {
    return {
      selectItems,
      item: defaultActive
    }
  }
}
</script>

<style>

</style>



