<template>
  <div>
    <input type="file" hidden id="hidden-file-input" name="pic" ref="fileInput"
      @change="onFileChange($event.target)" />
    <v-btn
      fab
      color="blue"
      dark
      top
      right
      fixed
      @click="trigger"
    >
      <v-icon>add</v-icon>
    </v-btn>
    <v-container fluid>
      <v-layout row>
        <v-flex md1>
        </v-flex>
        <v-flex md10>
          <v-container grid-list-xl>
            <v-layout wrap>
              <v-flex md4 sm12 v-for="(it, index) in items" :key="index">
                <v-card>
                  <v-img :src="it.path" height="260px">
                  </v-img>
                  <v-card-actions>
                    <v-btn icon 
                      v-bind:dark="isAvatars[index]" 
                      :color="isAvatars[index] ? 'blue' : 'grey lighten-4'"
                      @click="changeAvatar(it.id)">
                      <v-icon>done_outline</v-icon>
                    </v-btn>
                    <v-btn dark icon color="blue">
                      <v-icon>build</v-icon>
                    </v-btn>
                    <v-btn icon dark color="red" @click="delMedia(it.id)">
                      <v-icon>clear</v-icon>
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
        <v-flex md1></v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import { uploadFile, getMedias, setAvatar, removeMedia } from '../services'

export default {
  props: ['lahan-id'],
  data () {
    return {
      file: null,
      items: []
    }
  },

  computed: {
    isAvatars () {
      return this.items.map(it => {
        return it.avatar
      })
    }
  },

  methods: {
    trigger () {
      this.$refs.fileInput.click()
    },
    onFileChange (ev) {
      this.file = ev.files[0]
      console.log(this.file)
      let fd = new FormData()
      fd.append('image', this.file, this.file.name)
      fd.append('id', this.lahanId)

      uploadFile(fd)
        .then(x => {
          console.log(x)
        })
        .catch(err => {
          console.log(err)
        })
        .then(() => {
          this.loadMedias()
        })
    },
    loadMedias () {
      getMedias(this.lahanId)
        .then(items => {
          this.items = items
        })
        .catch(err => {
          console.log(err)
        })
    },

    delMedia (id) {
      removeMedia(this.lahanId, id)
        .then(() => {
          alert('sukses')
          this.loadMedias()
        })
        .catch(err => {
          alert('gagal')
          console.log(err)
          this.loadMedias()
        })
    },

    changeAvatar (mid) {
      setAvatar(mid)
        .catch(err => {
          console.log(err)
        })
        .then(() => this.loadMedias())
    }
  },
  created () {
    this.loadMedias()
  }
}
</script>

<style>

</style>



