import { sistemPengairanEnum, sumberAirEnum,
        kemiringanLahanEnum,
        teksturTanahEnum, corganikEnum,
        bahayaBencanaEnum } from '../../services'

function makeSelect (enumDescriptor) {
  return {
    text: enumDescriptor[0],
    value: enumDescriptor[1]
  }
}

export const selectItems = {
  sistemPengairan: sistemPengairanEnum.map(makeSelect),
  sumberAir: sumberAirEnum.map(makeSelect),
  kemiringanLahan: kemiringanLahanEnum.map(makeSelect),
  teksturTanah: teksturTanahEnum.map(makeSelect),
  corganik: corganikEnum.map(makeSelect),
  bahayaBencana: bahayaBencanaEnum.map(makeSelect)
}



