<template>
  <v-container>
    <v-layout>
      <v-flex md10 offset-md1>
        <v-toolbar dark color="pink lighten-1">
          <v-toolbar-title>Rating Kriteria</v-toolbar-title>
        </v-toolbar>
        <v-btn fab dark fixed top right color="blue"
          @click="saveCritRank">Save</v-btn>
        <v-data-table
          id="crit-table"
          :headers="headers"
          :items="critRanks"
          hide-actions
          class="elevation-1"
        >
          <template slot="items" slot-scope="props">
            <td>{{ props.item.name }}</td>
            <td class="text-xs-left">
              <v-select
                :items="options"
                v-model="props.item.ling"></v-select>
            </td>
            <td class="text-xs-left">{{ formatRate(props.item.ling) }}</td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { ratingOptions, ratingList, saveRating } from '../services'

const RatingRule = [
  [ 'Sangat Rendah', [0, 0, 0.25] ],
  [ 'Rendah', [0, 0.25, 0.5] ],
  [ 'Cukup Tinggi', [0.25, 0.5, 0.75] ],
  [ 'Tinggi', [0.5, 0.75, 1] ],
  [ 'Sangat Tinggi', [0.75, 1, 1] ]
]

export default {
  data () {
    return {
      options: [],
      critRanks: [],
      headers: [
        { text: 'Nama', value: 'nama' },
        { text: 'Nilai Linguistik', value: 'ling' },
        { text: 'Nilai Triangular', value: 'value' }
      ]
    }
  },

  methods: {
    async loadOptions () {
      try {
        this.options = (await ratingOptions()).map(opt => {
          return {
            text: opt.ling,
            value: opt.ling
          }
        })
      } catch (err) {
        console.log(err)
      }
    },
    async loadRatingList () {
      try {
        this.critRanks = await ratingList()
      } catch (err) {
        console.log(err)
      }
    },
    formatRate (r) {
      for (let index = 0; index < RatingRule.length; index++) {
        const element = RatingRule[index]
        if (r === element[0]) {
          return element[1]
        }
      }
      return ''
    },
    buildCritRank () {
      let result = {}
      this.critRanks.forEach(cr => {
        result[`${cr.name}`] = cr.ling
      })
      return result
    },
    saveCritRank () {
      let payload = this.buildCritRank()
      saveRating(payload)
        .then(() => {
          this.loadRatingList()
        })
        .catch(err => {
          console.log(err)
        })
    }
  },

  mounted () {
    this.loadOptions()
    this.loadRatingList()
  }
}
</script>

<style>
#crit-table tr td {
  font-size: 16px;
}
</style>



