<template>
  <div>
    <v-snackbar
      v-model="snack.success"
      top>
      Sukses mengubah data
    </v-snackbar>

    <v-snackbar
      v-model="snack.fail"
      top>
      Gagal mengubah data
    </v-snackbar>

    <v-tabs grow>
      <v-tab ripple key="tab-data">Data</v-tab>
      <v-tab ripple key="tab-galeri">Galeri</v-tab>
      <v-tab ripple @click="$emit('close')">Back</v-tab>

      <v-tab-item key="tab-data">
        <v-container>
          <v-layout row>
            <v-flex md8 offset-md2>

              <v-btn dark large block color="blue" class="my-4"
                @click="save">Simpan</v-btn>

              <v-text-field 
                label="Lokasi"
                v-model="activeLahan.lokasi"></v-text-field>

              <v-text-field 
                label="Longitude"
                type="number"
                v-model="activeLahan.long"></v-text-field>

              <v-text-field 
                label="Latitude"
                type="number"
                v-model="activeLahan.lat"></v-text-field>

              <v-text-field 
                label="Curah Hujan"
                type="number"
                v-model="activeLahan.curahHujan"
                readonly></v-text-field>

              <v-select 
                :items="enums.sp" 
                label="Sistem Pengairan" 
                v-model="activeLahan.sistemPengairan">
              </v-select>

              <v-select :items="enums.sa" label="Sumber Air"
                v-model="activeLahan.sumberAir">
              </v-select>

              <v-select :items="enums.kl" label="Kemiringan Lahan"
                v-model="activeLahan.kemiringanLahan">
              </v-select>

              <v-select :items="enums.tt" label="Tekstur Tanah"
                v-model="activeLahan.teksturTanah">
              </v-select>
              <v-select :items="enums.co" label="C-Organik"
                v-model="activeLahan.corganik">
              </v-select>
              <v-select :items="enums.bb" label="Bahaya Bencana"
                v-model="activeLahan.bahayaBencana">
              </v-select>

              <v-text-field 
                label="Kedalaman Tanah"
                type="number"
                v-model="activeLahan.kedalamanTanah">
              </v-text-field>
            </v-flex>
          </v-layout>

        </v-container>
      
      </v-tab-item>

      <v-tab-item key="tab-galeri">
        <galeri-view :lahan-id="activeId"/>
      </v-tab-item>

    </v-tabs>
  </div>
</template>

<script>
import { sistemPengairanEnum, sumberAirEnum, kemiringanLahanEnum,
teksturTanahEnum, corganikEnum, bahayaBencanaEnum, getById, update } from '../services'
import GaleriView from './GaleriView'

const defaultActive = {
  lokasi: 'oesapa',
  avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
  lat: -10.179173,
  long: 123.591715,
  curahHujan: 448,
  sistemPengairan: 'Irigasi Permukaan',
  sumberAir: 'Banyak',
  kemiringanLahan: 'Datar',
  teksturTanah: 'Halus',
  corganik: 'Tersedia',
  bahayaBencana: 'Ringan',
  kedalamanTanah: 48,
  picture: 'https://cdn.vuetifyjs.com/images/cards/house.jpg'
}

function makeSelect (arr) {
  return {
    text: arr[0],
    value: arr[1]
  }
}

export default {
  props: [ 'activeId' ],

  components: {
    GaleriView
  },

  data () {
    return {
      x: 0,
      activeLahan: defaultActive,
      enums: {
        sp: sistemPengairanEnum.map(makeSelect),
        sa: sumberAirEnum.map(makeSelect),
        kl: kemiringanLahanEnum.map(makeSelect),
        tt: teksturTanahEnum.map(makeSelect),
        co: corganikEnum.map(makeSelect),
        bb: bahayaBencanaEnum.map(makeSelect)
      },
      snack: {
        success: false,
        fail: false
      }
    }
  },

  mounted () {
    getById(this.activeId)
      .then(item => {
        this.activeLahan = item
      })
      .catch(err => {
        console.log(err)
      })
  },

  methods: {
    async save () {
      let result = await update(this.activeId, this.activeLahan)
      if (result.status !== 200) {
        this.snack.fail = true
        this.snack.success = false
      } else {
        this.snack.fail = false
        this.snack.success = true
      }
    }
  }
}
</script>

<style>

</style>



