import * as axios from 'axios'

const baseUrl = 'http://localhost:3000'

export function save (item) {
  return axios.post(`${baseUrl}/api/lahan`, item)
    .then(resp => resp.status)
}

export function getAllData () {
  return axios.get(`${baseUrl}/api/lahan`)
    .then(resp => resp.data)
}

export function getById (id) {
  return axios.get(`${baseUrl}/api/lahan/${id}`)
    .then(resp => resp.data)
}

export function update (id, payload) {
  return axios.put(`${baseUrl}/api/lahan/${id}`, payload)
}

export function remove (id) {
  return axios.delete(`${baseUrl}/api/lahan/${id}`)
}

export function uploadFile (fd) {
  let url = `${baseUrl}/media`
  return axios.post(url, fd)
    .then(x => x.data)    
}

export function getMedias (id) {
  let url = `${baseUrl}/lahan/${id}/media`
  return axios.get(url)
    .then(x => x.data)
}

export function removeMedia (lid, mid) {
  let url = `${baseUrl}/delete-media/${lid}/${mid}`
  return axios.get(url)
    .then(x => {
      if (x.status !== 500) {
        return null
      } else {
        throw new Error('Can not delete media')
      }
    })
}

export function ratingOptions () {
  let url = `${baseUrl}/criteria_rating_options`
  return axios.get(url)
    .then(x => x.data)
}

export function ratingList () {
  let url = `${baseUrl}/criteria_rating`
  return axios.get(url)
    .then(x => x.data)
}

export function saveRating (payload) {
  let url = `${baseUrl}/criteria_rating`
  return axios.post(url, payload)
    .then(x => x.data)
}

export function setAvatar (mid) {
  let url = `${baseUrl}/avatar/${mid}`
  return axios.get(url)
    .then(x => x.data)
}

export function getAvatar (lid) {
  let url = `${baseUrl}/lahan/${lid}/avatar`
  return axios.get(url)
    .then(x => x.data)
}

